.login {
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: rgb(238, 77, 45);
  transform: translateX(-21px);
}

.login__nav {
  width: 100vw;
  height: 5rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
  gap: 300px;
  background: #fff;
}
.login__nav__logo {
  color: black;
  align-items: center;
}
.login__nav__logo h4 {
  transform: translateY(10px);
}
.login__nav__logo img {
  background: transparent;
  width: 140px;
  height: 45px;
}

.login__nav__help {
  color: #ee4d2d;
  font-size: 1.125rem;
  margin-right: 0.9375rem;
  cursor: pointer;
}

.login__section {
  height: 37.5rem;
  width: 100vw;
  background-image: url("https://cf.shopee.ph/file/62d6ceb39c9ce4f7f22bd174550c5a64");
  background-repeat: no-repeat;
  background-position: center center;
}

#login__form {
  display: block;
  position: absolute;
  background-color: #fff;
  box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.14);
  border-radius: 0.25rem;
  overflow: hidden;
  width: 31.25rem;
  min-height: 29.6875rem;
  top: 150px;
  left: 820px;
}

.login__form__main {
  display: flex;
  flex-direction: column;
}
#login__login {
  margin-left: 15px;
  width: 500px;
  height: 80px;
  align-items: center;
  color: rgb(46, 45, 45);
}

#login__form__input__1 {
  height: 2.5rem;
  overflow: hidden;
  border: 1px solid rgba(0, 0, 0, 0.14);
  border-radius: 2px;
  box-shadow: inset 0 2px 0 rgba(0, 0, 0, 0.02);
  margin: 0 20px;
  margin-bottom: 25px;
}
#login__form__input__2 {
  height: 2.5rem;
  overflow: hidden;
  border: 1px solid rgba(0, 0, 0, 0.14);
  border-radius: 2px;
  box-shadow: inset 0 2px 0 rgba(0, 0, 0, 0.02);
  margin: 0 20px;
  margin-bottom: 35px;
}

#login__form__input__2 > .eye__icon {
  width: 22px;
  height: 22px;
  margin: auto;
  opacity: 0.6;
}

#login__form__input__1 > input {
  padding: 0.75rem;
  outline: none;
  border: none;
  flex: 1;
  filter: none;
}
#login__form__input__2 > input {
  padding: 0.75rem;
  outline: none;
  border: none;
  flex: 1;
  filter: none;
}

#login__button > button {
  color: #fff;
  background-color: #ee4d2d;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.09);
  opacity: 0.7;
  cursor: not-allowed;
  height: 2.5rem;
  min-width: 8.75rem;
  width: 95%;
  margin: auto;
  margin-bottom: 10px;
}

#login__links {
  display: flex;
  justify-content: space-between;
  font-weight: 500;
  margin: 0 15px;
}

#login__links > a {
  text-decoration: none;
}

#login__or {
  color: rgb(46, 45, 45);
  padding: 1rem 1rem;
  text-transform: uppercase;
  font-weight: 700;
  display: flex;
  align-items: center;
}
#login__or > span {
  width: 200px;
  margin: auto;
  border-bottom: 2px solid rgb(117, 117, 117);
}

#login__socialmedia {
  display: flex;
  width: 95%;
  margin: auto;
  justify-content: space-around;
  margin-bottom: 40px;
}

#login__socialmedia > button {
  width: 140px;
  height: 40px;
  color: #fff;
  font-size: 1rem;
  font-weight: 500;
  background-color: #4285f4;
  display: flex;
  justify-content: space-between;
  padding: 0 20px 0 5px;
  align-items: center;
}

#login__socialmedia :nth-child(3) {
  background-color: #000;
}

#g__span {
  margin-left: -3px;
  width: 35px;
  height: 32px;
  background: #fff;
}

#G {
  font-weight: bolder;
  margin: auto;
  transform: translateY(-2px);
  background: -webkit-linear-gradient(
    left,
    rgba(231, 52, 39, 1) 0%,
    rgba(231, 52, 39, 1) 10%,
    rgba(236, 245, 50, 1) 42%,
    rgba(50, 245, 70, 1) 68%,
    rgba(76, 50, 245, 1) 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

#sign__up {
  display: flex;
  justify-content: center;
  text-align: center;
  font-weight: 500;
  color: gray;
}
#sign__up > span {
  color: #ee4d2d;
  font-weight: 700;
}
