.header {
  position: fixed;
  z-index: 222;
  top: 0;
  left: 0;
  right: 0;
  background: linear-gradient(-180deg, #f53d2d, #f63);
}

.header__navbar {
  height: 2.125rem;
  padding-top: 10px;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  vertical-align: auto;
  line-height: 1.2;
  max-width: 1200px;
  margin: auto;

  margin-top: 2px;
  transform: translateX(15px);
}

.header a:hover {
  text-decoration: none;
  color: #fff;
}

.header__navbar__links {
  display: flex;
  align-items: center;
}

.header__navbar__links__left {
  display: flex;
  align-items: center;
}

.header__navbar__links__left a {
  border-right: 2px solid hsla(0, 0%, 100%, 0.22);
}

.header__navbar__links__right :nth-child(3) {
  border-right: 2px solid hsla(0, 0%, 100%, 0.22);
}

.header__navbar__links__left a,
div {
  display: flex;
  font-size: 0.8125rem;
  font-weight: 300;
  text-decoration: none;
  padding: 0 0.4375rem;
  color: #fff;
}

.header__navbar__links__left a:hover {
  opacity: 0.6;
}

.header__navbar__links__right {
  display: flex;
  align-items: center;
  cursor: pointer;
}
.header__navbar__links__right *:hover {
  opacity: 0.6;
}

.header__navbar__links__right a,
li {
  text-decoration: none;
  display: flex;
  font-size: 0.8125rem;
  font-weight: 300;
  padding: 0 0.625rem;
  color: #fff;
  list-style: none;
  align-items: center;
}

.header__FacebookIcon,
.header__InstagramIcon,
.header__NotificationsIcon,
.header__HelpIcon {
  width: 22px !important;
  height: 22px !important;
  margin-right: 10px;
}

.header__icons {
  flex-direction: row;
}

.header__navbar__bottom {
  position: relative;
  margin-right: auto;
  margin-left: auto;
  display: flex;
  align-items: center;
  align-content: center;
  max-width: 1200px;
  justify-content: space-between;
  padding: 1rem 0 0.625rem;
  transform: translateX(20px);
}

.header__navbar__bottom__logo {
  width: 140px;
  height: 50px;
  color: #fff;
  margin: 0 40px 0 0;
}

.header__navbar__bottom__search {
  display: flex;
  flex-direction: column;
  max-width: 70%;
  flex: 1;
}

.search__bar {
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.09);
  display: flex;
  -webkit-box-align: stretch;
  align-items: stretch;
  -webkit-box-pack: justify;
  justify-content: space-between;
  height: 2.5rem;
  box-sizing: border-box;
  padding: 0.1875rem;
  border-radius: 2px;
  background: #fff;
}
.search__bar__main {
  display: flex;
  background-color: #fff;
  border-color: #fff;
  box-sizing: border-box;
  padding-left: 0.625rem;
}

.search__bar__main input {
  align-items: center;
  outline: none;
  border: none;
  padding: 0;
  margin: 0;
  cursor: text;
}

.search__bar__button {
  background: #fb5533;
  height: 34px;
  padding: 0 15px;
  min-width: 60px;
  max-width: 190px;
  color: #fff;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.09);
  border-radius: 2px;
  border: 0;
}

.search__bar__button:hover {
  opacity: 0.8;
  cursor: pointer;
  text-decoration: none;
}

.search__bar__bottom {
  width: 100%;
}
.search__bar__bottom__links {
  margin-top: 5px;
  height: 0.875rem;
  font-size: 0.75rem;
  font-weight: 300;
  line-height: 0.875rem;
  flex-wrap: wrap;
  text-decoration: none;

  display: flex;
  flex-shrink: 0;
}
.search__bar__bottom__links a {
  text-decoration: none;
  margin: 0 13px 0 0;
  cursor: pointer;
  color: #fff;
}

.header__navbar__bottom__cart {
  position: relative;
  justify-self: flex-end;
  display: flex;
  margin: auto;
  padding: 10px 0;
  -webkit-box-align: center;
}

.header__navbar__bottom__cart__icon {
  width: 30px !important;
  height: 30px !important;
  cursor: pointer;
}
