.shopee {
  position: relative;
  top: 120px;
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 900px;
}

.banner {
  display: flex;
  height: 400px;
  flex-direction: column;
  width: 100vw;
  background-color: #fff;
}

.banner__top {
  position: relative;
  display: flex;
  align-items: stretch;
  justify-content: center;
  margin: auto;
  margin-top: 25px;
  transform: translateX(-10px);
}

.banner__top__left {
  position: relative;
  align-items: center;
  margin-right: auto;
  line-height: 0;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.05);
  border-radius: 0.125rem;
  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
  min-width: 850px;
  max-width: 850px;
  flex: 0 0 2;
  overflow: hidden;
  object-fit: cover;
}

.banner__top__left .carousel-inner {
  overflow: hidden;
}

.banner__top:hover .carousel-control-prev {
  visibility: visible;
}
.banner__top:hover .carousel-control-next {
  visibility: visible;
}

.banner__top__left .carousel-control-prev {
  visibility: hidden;
  margin: 0 21px;
  padding: 0 -100px;
  width: 40px;
  height: 70px;
  top: 80px;
  background-color: #222;
}
.banner__top__left .carousel-control-next {
  visibility: hidden;
  margin: 0 21px;
  padding: 0 -100px;
  width: 40px;
  height: 70px;
  top: 80px;
  background: #222;
}

.banner__top__left .carousel-item img {
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 3px;
  object-fit: cover;
}

.banner__top__right {
  flex: 0 0 1;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
  gap: 5px;
  transform: translateX(-30px);
}

.banner__top__right img {
  max-width: 400px;
  max-height: 116.5px;
  height: 116.5px;
  border-radius: 3px;
}

.banner__bottom {
  width: 1218px;
  height: 105px;
  transform: translate(-14px, 10px);
  display: flex;
  justify-content: center;
  padding: 20px 8px;
  margin: auto;

  background: #fff;
}

.banner__bottom__links {
  display: flex;
}

.banner__bottom__links li {
  display: flex;
  flex-direction: column;
  color: #222;
  font-weight: 400;
  cursor: pointer;
}
.banner__bottom__links li:hover {
  transform: translateY(-2px);
}

.banner__bottom__links li img {
  width: 45%;
  height: auto;
}

.banner_welcome {
  margin-top: 20px;
  margin: auto;
  height: 110px;
  width: 1200px;
  background-size: cover;
  overflow: hidden;
  cursor: pointer;
  background-image: url("https://cf.shopee.ph/file/a36acafad6f458afcaf4d48d16d2cda2_xxhdpi");
}

.shopee__category {
  max-width: 1200px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  background: cornsilk;
  margin: auto;
}
.shopee__category * {
  margin: 0;
  padding: 0;
}

.shopee__category header {
  padding: 10px;
  color: #222;
}

.shopee__category__carousel {
  display: flex;
}
.shopee__category__carousel .carousel-indicators {
  display: none;
}

.shopee__category__carousel .carousel-control-prev {
  display: none;
}

.shopee__category__carousel .carousel-control-next {
  top: 47%;
  width: 20px;
  height: 20px;
  padding: 5px;
  border-radius: 50%;
  background-color: #222;
  transition: 0.3s;
}
.shopee__category__carousel:hover .carousel-control-next {
  transform: scale(2);
  transition: 0.3s;
}

.shopee__category__carousel__main {
  display: flex;
  flex-direction: column;
  max-width: 1200px;
  transform: translateX(-10px);
}

.shopee__category__carousel__main img {
  height: 83px;
  width: 76px;
}

.shopee__category__carousel__top div:hover {
  -webkit-box-shadow: 2px 1px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 2px 1px 5px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 2px 1px 5px 0px rgba(0, 0, 0, 0.75);
}
.shopee__category__carousel__bottom div:hover {
  -webkit-box-shadow: 2px 1px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 2px 1px 5px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 2px 1px 5px 0px rgba(0, 0, 0, 0.75);
}
.shopee__category__carousel__top div {
  display: flex;
  flex-direction: column;
  text-align: center;
  color: #222;
  height: 150px;
  width: 120px;
  border: 1px solid rgba(0, 0, 0, 0.05);
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.shopee__category__carousel__bottom div {
  display: flex;
  flex-direction: column;
  text-align: center;

  color: #222;
  height: 150px;
  width: 120px;
  border: 1px solid rgba(0, 0, 0, 0.05);
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
